a{
  position: relative;
  height: 100%;
  text-decoration: none;
}
li {
  list-style-type: none;
}
ul {
  padding: 0;
}
.home-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.navlink{
  font-size: .95em;
  color: rgb(252, 246, 246);
  text-transform: uppercase;
  padding: 1em 3em;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  background-color: 'red';
  width: 100%;
}

.active, a:hover {
  color: rgb(192, 224, 116) !important;
  text-decoration: none !important;
}

.button {
  color: #e6e6e6;
  background: #0a0a0a;
  border: none;
  font-size: 16px;
  border-radius: 3px;
  width: 200px;
  text-align: center;
  display: block;
  padding: 7px 0;
  margin: 10px auto;
}

.languages {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.languages li {
  margin: 10px;
  font-weight: 700;
  cursor: pointer;
}
.avatar {
  width: 150px;
}
.popular-item {
  margin: 20px;
  text-align: center;
}
.popular-rank {
  font-size: 20px;
  margin: 10px;
}
.popular-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.space-list-items {
  margin-bottom: 7px;
}
.header {
  text-align: center;
  font-size: 30px;
  font-weight: 200;
}
.line {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  margin: 5px;
  background-color: #2e7dbd;
}

.load-3 .line:nth-last-child(1) {-webkit-animation: loadingC .6s .1s linear infinite;animation: loadingC .6s .1s linear infinite;}
.load-3 .line:nth-last-child(2) {-webkit-animation: loadingC .6s .2s linear infinite;animation: loadingC .6s .2s linear infinite;}
.load-3 .line:nth-last-child(3) {-webkit-animation: loadingC .6s .3s linear infinite;animation: loadingC .6s .3s linear infinite;}

@-webkit-keyframes loadingC {
  0% {-webkit-transform: translate(0,0);transform: translate(0,0);}
  50% {-webkit-transform: translate(0,20px);transform: translate(0,20px);}
  100% {-webkit-transform: translate(0,0);transform: translate(0,0);}
}

@keyframes loadingC {
  0% {-webkit-transform: translate(0,0);transform: translate(0,0);}
  50% {-webkit-transform: translate(0,20px);transform: translate(0,20px);}
  100% {-webkit-transform: translate(0,0);transform: translate(0,0);}
}


